*,
*:before,
*:after {
  box-sizing: border-box; }

body {
  min-height: 100vh;
  background: url("https://source.unsplash.com/random/1600x900") no-repeat center center fixed;
  background-size: cover;
  font: 14px/20px "Lato", Arial, sans-serif;
  color: #9e9e9e;
  margin-top: 30px; }

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0; }

.apptitle {
